<template>
  <div style="width: 80%;margin-left: 10%;">
    <div style="width: 100%;">
      <div class="get_message">
        <h2>请输入接口信息</h2>
        <el-select style="width: 200px;margin-left:5px;" v-model="queryType" slot="prepend" class="filter-item" @change="changepara">
          <el-option v-for="(item,index) in chooseList"
                     :key="index"
                     :value="item.value"
                     :label="item.label"></el-option>
        </el-select>
        <div class="get_parameter">
          <!--<el-input v-for="(item,index) in para" :key="index" :value="item" style="width: 200px;" :v-model="paramodel[index]"></el-input>-->
          <ul>
            <li v-for="(item,index) in para" :key="index" :value="item" style="width: 200px;float: left;">
              {{item}}
              <div v-show="ifshow[index]" style="width: 200px;height: 60px;">
                <el-input v-model="paramodel[index]" style="width: 180px;"></el-input>
              </div>
              <div v-show="ifshow1[index]" style="width: 200px;height: 60px;">
                <!--style="border:5px solid red;"-->
                <template>
                  <el-radio v-model="paramodel[index]" label="0">0</el-radio>
                  <el-radio v-model="paramodel[index]" label="1">1</el-radio>
                </template>
              </div>
            </li>
          </ul>
        </div>
        <div style="width: 100%;">
          <h4>生成接口: </h4>
          <el-input v-model="getinter" style="width: 90%;margin:10px;"></el-input>

          <div style="width: 90%;margin:10px;background-color:#c5c5c5;height: 100px;">{{getinterpara}}</div>
        </div>
        <el-button @click="getinterface" style="margin-left: 10px;">生成接口</el-button>
        <el-button @click="showData">提交接口</el-button>
        <el-button @click="clearData">清空数据</el-button>
        <el-button @click="analyzepara">分析参数</el-button>
        <el-button @click="analyzeData">分析数据</el-button>
        <el-button @click="getchooseList">测试数据</el-button>
      </div>
      <div class="analyze_parameter">
        <h4>参数分析: </h4>
        <div class="analyze_Data"><p id="Data_para" style="visibility: hidden;color: red;">参数输入有误: {{Datapara.error}}<br/>{{Datapara.list}}</p></div>
      </div>
      <div class="return_message">
        <h2>接口返回结果</h2>
        <div class="return_Data" v-loading="loading1" element-loading-text="拼命加载中"><p>{{Data}}</p></div>
        <div class="return_Data1" v-loading="loading2" element-loading-text="拼命加载中"><p id="Data1" style="visibility: hidden;">{{Data1}}</p></div>
      </div>
    </div>
  </div>
</template>

<script>
let Base64 = require('js-base64').Base64
export default {
  name: 'TimeCloud',
  data () {
    return {
      ifshow: [],
      ifshow1: [],
      loading1: false,
      loading2: false,
      chooseList: [], // 选择查询条件
      Data: '', // 显示查询原始数据
      Data1: {
        imei: '',
        type: '',
        msg: []
      }, // 显示分析数据
      queryType: '', // 绑定选择条件
      para: [], // 显示查询参数
      paraType: '', // 查询参数类型
      Datapara: {
        error: '',
        false: [],
        list: ''
      }, // 显示分析参数的结果
      paramodel: [], // 显示查询参数的输入值
      getinter: '', // 生成接口
      getinterpara: {
        imei: ''
      },
      eventcode: '', // 接口数据事件分析
      eventmsg: '' // 接口数据具体信息分析
    }
  },
  methods: {
    getchooseList () {
      this.$http.get('./../dist/static/inter.json').then(res => {
      // this.$http.get('http://localhost:8080/static/inter.json').then(res => {
        // console.log(res)
        this.chooseList = res.data.list // 接口+参数
        this.Data = res.data.data // 测试数据
        this.eventcode = res.data.event // 事件代码
        this.eventmsg = res.data.msg // 信息解析
        this.paraType = res.data.parameter // 参数信息
        this.analyzeData()
      })
    }, // 导入查询条件
    showData () {
      this.analyzepara()
      this.getinterface()
      if (this.Datapara.error !== 0) {
        return this.$message.error('参数有误,详情请见参数分析')
      }
      let t = this.getinter
      if (!t) {
        return this.$message.error('接口为空，请先生成接口')
      }
      var appKey = 'b994ec9e4e2b4cdca1631599f602d859'
      var appSecret = '1d31a09cb99fe97ba5092e9f559f78f3'
      var auth = Base64.encode(appKey + ':' + appSecret)
      this.loading1 = true
      // this.$http({
      //   timeout: 8000,
      //   method: 'post',
      //   url: t,
      //   headers: {
      //     Authorization: 'Basic ' + auth
      //   }
      // }).then(res => {
      //   this.Data = res
      //   this.loading1 = false
      //   this.analyzeData()
      // }).catch(err => {
      //   console.log(err)
      //   this.Data = err
      //   this.loading1 = false
      // })
      this.$http({
        timeout: 5000,
        method: 'post',
        url: 'http://www.lbs-iot.com:8082/third/api/v1/device/battery',
        headers: {
          Authorization: 'Basic ' + auth
        },
        data: this.getinterpara
      }).then(res => {
        this.Data = res.data
        // this.Data = res
        this.loading1 = false
        this.analyzeData()
      }).catch(err => {
        // console.log(err)
        if (err.config.timeout === 5000) {
          this.Data = '请求超时'
          this.loading1 = false
        } else {
          this.Data = err
          this.loading1 = false
        }
      })
    }, // 获取查询数据
    clearData () {
      this.para = []
      this.Data = ''
      this.Data1 = {
        imei: '',
        type: '',
        msg: []
      }
      this.Datapara = {
        error: '',
        false: [],
        list: ''
      }
      this.queryType = ''
      this.getinter = ''
      document.getElementById('Data_para').style.visibility = 'hidden'
      document.getElementById('Data1').style.visibility = 'hidden'
    }, // 清空参数 原始数据 解析数据
    analyzeData () {
      this.loading2 = true
      // this.Data1 = []
      this.Data1 = {
        imei: '',
        type: '',
        msg: {}
      }
      this.Data1.imei = this.Data.imei
      document.getElementById('Data1').style.visibility = 'visible'
      for (var i = 0; i < this.eventcode.length; i++) {
        if (this.eventcode[i].code - this.Data.type === 0) {
          this.Data1['type'] = this.eventcode[i].type
        } // 数字相等的判断好像还有问题
      }
      for (var j in this.Data.msg) {
        for (var m = 0; m < this.eventmsg.length; m++) {
          if (j === this.eventmsg[m].参数名称) {
            // var item = {[this.eventmsg[m].描述]: this.Data.msg[j]}
            // this.Data1.msg.push(this.eventmsg[m].描述 + ':' + this.Data.msg[j])
            // this.Data1.msg.push(this.eventmsg[m].描述)
            this.Data1.msg[this.eventmsg[m].描述] = this.Data.msg[j]
          }
        }
      }
      this.loading2 = false
    }, // 分析数据
    changepara () {
      this.paramodel = []
      this.para = []
      for (var i = 0; i < this.chooseList.length; i++) {
        if (this.queryType === this.chooseList[i].value) {
          this.para = this.chooseList[i].parameter.split(',')
          this.paramodel.length = this.para.length
          // console.log(this.para)
          // console.log(this.paramodel)
        }
      }
      for (var m = 0; m < this.para.length; m++) {
        for (var n = 0; n < this.paraType.length; n++) {
          if (this.para[m] === this.paraType[n].parameter) {
            if (this.paraType[n].type === 'bool') {
              this.ifshow[m] = false
              this.ifshow1[m] = true
            } else {
              this.ifshow[m] = true
              this.ifshow1[m] = false
            }
            // document.getElementById('idname').style.visibility = 'visible'
          }
        }
      }
    }, // 改变查询条件 显示参数
    getinterface () {
      this.getinter = ''
      this.getinterpara = {}
      for (var i = 0; i < this.chooseList.length; i++) {
        if (this.queryType === this.chooseList[i].value) {
          this.getinter = this.chooseList[i].URL
        }
      }
      for (var j = 0; j < this.paramodel.length; j++) {
        if (!this.paramodel[j]) { continue }
        // this.getinter += this.para[j] + '=' + this.paramodel[j]
        console.log('this.para[j]=' + this.para[j] + ' this.paramodel[j] =' + this.paramodel[j])
        // this.getinterpara.imei = '12312'
        // console.log(this.getinterpara)
        this.getinterpara[this.para[j]] = this.paramodel[j]
      }
      console.log(this.getinterpara)
      // for (var j = 0; j < this.paramodel.length; j++) {
      //   if (!this.paramodel[j]) { continue }
      //   if (this.paramodel[j - 1]) { this.getinter += '&' }
      //   if (j === 0) { this.getinter += '?' }
      //   this.getinter += this.para[j] + '=' + this.paramodel[j]
      // }
    }, // 获取接口
    analyzepara () {
      this.Datapara.error = 0
      this.Datapara.list = []
      this.Datapara.false = []
      document.getElementById('Data_para').style.visibility = 'visible'
      for (var i = 0; i < this.para.length; i++) {
        for (var j = 0; j < this.paraType.length; j++) {
          if (this.para[i] === this.paraType[j].parameter) {
            var pattern = this.getpattern(this.paraType[j])
            if (this.paraType[j].need === 'TRUE') {
              this.geterror(this.paraType[j], pattern, this.paramodel[i])
            } else {
              if (this.paramodel[i]) {
                this.Datapara.false.push(this.paraType[j].parameter)
                this.geterror(this.paraType[j], pattern, this.paramodel[i])
              }
            }
          }
        }
      }
      if (this.queryType === 'callin') {
        if (this.Datapara.false.length === 1) {
          this.Datapara.error += 0
        } else {
          this.Datapara.error += 1
          this.Datapara.list.push(this.Datapara.error + ': firewall,ring,viberate,nodisturb_time 有且仅有一项')
        }
      }
    }, // 用正则表达式分析参数
    getpattern (parameter) {
      if (parameter.parameter === 'ip') {
        return new RegExp('^(?:(?:2[0-4][0-9]\\.)|(?:25[0-5]\\.)|(?:1[0-9][0-9]\\.)|(?:[1-9][0-9]\\.)|(?:[0-9]\\.)){3}(?:(?:2[0-5][0-5])|(?:25[0-5])|(?:1[0-9][0-9])|(?:[1-9][0-9])|(?:[0-9]))$')
      } else {
        return new RegExp(parameter.RegExp)
      }
    }, // 获取正则表达式
    geterror (parameter, pattern, inputtext) {
      if (inputtext) {
        if (pattern.test(inputtext)) {
          this.Datapara.error += 0
        } else {
          this.Datapara.error += 1
          this.Datapara.list.push(this.Datapara.error + ' : ' + parameter.parameter + '的输入形式应为' + parameter.type + '，描述为' + parameter.describe)
        }
      } else {
        this.Datapara.error += 1
        this.Datapara.list.push(this.Datapara.error + ' : ' + parameter.parameter + '不能为空，' + '描述为' + parameter.describe)
      }
    } // 获取参数错误方式
    // 学习改为提升， 描述具体工作职责，工作项目 运用语言
  },
  created () {
    this.getchooseList()
  }
}
</script>

<style scoped>
  div{
    float: left;
    border-radius: 10px;
    border: 1px;
  }
  li{
    list-style-type: none;
  }
  h4{
    float: left;
  }
  p{
    margin-left: 10px;
  }
  .get_message{
    width: 100%;
  }
  .get_parameter{
    width: 100%;
    min-height: 150px;
    min-width: 320px;
    background-color: RGB(220,230,241);
    margin-top:10px;
  }
  .return_message{
    width: 100%;
  }
  .return_Data{
    background-color: RGB(184,204,228);
    min-height: 600px;
    min-width: 320px;
    width: 50%;
    white-space: pre;
  }
  .return_Data1{
    background-color: RGB(159,189,225);
    /*background-color: RGB(149,179,215);*/
    min-height: 600px;
    min-width: 320px;
    width: 50%;
    white-space: pre;
    /*float: right;*/
  }
  .analyze_parameter{
    width: 100%;
  }
  .analyze_Data{
    width: 90%;
    min-height: 150px;
    min-width: 320px;
    background-color: RGB(184,204,228);
    margin-top:10px;
    white-space: pre;
    /*margin-left:10px;*/
  }
</style>



